.inner-item {
    display: flex;
    flex-direction: row;
    width: 90%;
    text-align: left;
    font-size: 16px;
    margin-bottom: 10px;
    padding: 10px;
}

.item-left {
    width: 30%;
}

.item-right {
    width: 100%;
}

.text-row-right {
    padding-left: 15px;
}

.align-side {
    display: flex;
    justify-content: space-between;
}